/* ------------------ components ------------------*/
.btn {
  width: 100%;
  height: 100vmax;
  outline: none;
  border-style: none; }
  .btn:focus {
    outline: 1px solid #0c0c0c; }
  .btn:hover {
    opacity: 0.7;
    border: 1px double black;
    color: #1e1e1e;
    outline: none;
    cursor: pointer; }
  .btn:active {
    opacity: 0.9;
    border: 2px inset white;
    color: black;
    outline: none; }

.link {
  text-decoration: none; }
  .link:visited {
    box-shadow: 1px 1px 2px black; }
  .link:focus {
    outline: 2px solid #363636; }
  .link:hover {
    opacity: 0.7;
    color: #c3471b;
    cursor: pointer; }
  .link:active {
    opacity: 1;
    color: #632b17; }

.input:focus {
  border: 1px solid black;
  background-color: white; }

.input:hover {
  cursor: pointer;
  border: 2px solid black;
  background-color: #f7eca4; }

